.la-wrapper {
  background: rgba($color: #000000, $alpha: 0.5);
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 100001;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow-y: auto;

  .la-content {
    position: relative;
    max-height: 100%;
    max-width: 70%;

    .la-close {
      position: absolute;
      right: -30px;
      top: 30px;
      cursor: pointer;
    }
    .la-image {
      border-radius: 0.5rem;
      cursor: default;
      object-fit: contain;
      height: 100%;
      width: 100%;
      margin: 2rem 0;
    }
  }
}
