// =========================== >>
// Widget Form
// =========================== >>

.form-group {
  margin-bottom: 1em;

  label {
    display: inline-block;
    font-size: 14px;
    margin-bottom: 0.45rem;
  }
}

.form-control {
  display: block;
  width: 100%;
  padding: 0.5rem 0.75rem;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  background: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: 4px;
  outline: 0;

  &.has-error {
    border-color: red;
  }
}

.custom-select {
  display: inline-block;
  width: 100%;
  height: calc(1.5em + 12px + 2px);
  padding: 6px 28px 6px 12px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  vertical-align: middle;
  background: #fff
    url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='4' height='5' viewBox='0 0 4 5'%3e%3cpath fill='%23343a40' d='M2 0L0 2h4zm0 5L0 3h4z'/%3e%3c/svg%3e")
    no-repeat right 12px center/8px 10px;
  border: 1px solid #ced4da;
  border-radius: 4px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
