// fade page transition
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.15s;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}

// modal transition
.modal-enter-active,
.modal-leave-active {
  opacity: 0;
}

.modal-enter-active .modal-content {
  transform: scale(0.75);
}

.modal-leave-active .modal-content {
  transform: scale(0.75);
}
