// =========================== >>
// Widget Body Suggestion
// =========================== >>

.suggestion {
  &-list {
    margin: 10px 0px 0px;
    padding: 0;
    list-style-type: none;
    display: flex;
    flex-wrap: wrap;
    margin-left: -10px;
    margin-top: -10px;

    &--item {
      flex: 0 0 auto;
      margin: 10px 0 0 10px;

      button {
        border: 1px solid #d97109;
        background-color: #d97109;
        border-radius: 5px;
        color: #fff;
        cursor: pointer;
        padding: 0.5rem 0.75rem;
        display: flex;
        align-items: center;

        p {
          margin: 0;
        }

        &:disabled {
          border-color: #bb630b;
          background-color: #bb630b;
          cursor: not-allowed;
        }
      }
    }
  }
}
