// Front Start
// =========================== >>

.front {
  height: 100%;
  background-color: $ui_color_secondary;
  overflow-y: auto;
  padding: 50px 30px 10px;

  h1 {
    color: #fff;
    margin: 10px 0px;
  }
}

.front-card {
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
  background: #fff;
  border-radius: 5px;
  padding: 20px;
  margin: 10px 0px;
}

.front-powered {
  color: #ffffff;
  font-size: 12px;
  text-align: center;
  // margin-top: auto;
  padding: 20px;
}

.text-danger {
  color: red;
}
