// =========================== >>
// Widget Footer: Form
// =========================== >>

.footer-form {
  display: flex;
  align-items: center;
  padding: 15px;

  button {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    background: none;
    border: none;
    height: 25px;
    width: 25px;
  }
}

.footer-input {
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;

  .input {
    border: 0;
    outline: none;
    width: 100%;
    margin-left: 5px;
    resize: none;
    line-height: 18px;
    height: 18px;
    max-height: 70px;
  }

  .editor {
    margin-left: 5px;
    width: 100%;
    word-break: break-word;
    max-height: 108px;
    overflow: auto;
    cursor: text;
  }
}

.footer-control {
  display: flex;
  align-items: center;
  justify-content: center;

  input[type="file"] {
    display: none;
  }
}
