// =========================== >>
// Widget Modals
// =========================== >>

.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: table;
  transition: opacity 0.3s ease;
}

.modal-wrapper {
  cursor: pointer;
  display: table-cell;
  vertical-align: middle;
}

.modal-content {
  cursor: default;
  width: 300px;
  margin: 0px auto;
  padding: 40px 30px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
  transition: all 200ms ease-in-out;
  position: relative;
}

.modal-content .modal-header {
}

.modal-content .modal-header .modal-close {
  cursor: pointer;
  position: absolute;
  top: 30px;
  right: 20px;
  border-radius: 100%;
}

.modal-content .modal-body {
}

.modal-content .modal-footer {
}
